import React from 'react';
import { Route, Switch } from 'react-router-dom';

import Login from './pages/Login';
import MealProgress from './pages/MealProgress';
import MealDetails from './pages/MealDetails';
import Meals from './pages/Meals';
import DrinkDetails from './pages/DrinkDetails';
import DrinkProgress from './pages/DrinkProgress';
import Drinks from './pages/Drinks';
import Explore from './pages/Explore';
import MealIngredients from './pages/MealIngredients';
import MealAreas from './pages/MealAreas';
import ExploreMeals from './pages/ExploreMeals';
import DrinkIngredients from './pages/DrinkIngredients';
import ExploreDrinks from './pages/ExploreDrinks';
import Profile from './pages/Profile';
import FinishedRecipes from './pages/FinishedRecipes';
import FavoriteRecipes from './pages/FavoriteRecipes';
import NotFound from './pages/NotFound';

import './App.css';

function App() {
  return (
    <div className="app">
      <Switch>
        <Route exact path="/" component={ Login } />
        <Route path="/meals/:id/in-progress" component={ MealProgress } />
        <Route
          path="/meals/:id"
          render={ (props) => <MealDetails { ...props } /> }
        />
        <Route path="/meals" component={ Meals } />
        <Route path="/drinks/:id/in-progress" component={ DrinkProgress } />
        <Route
          path="/drinks/:id"
          render={ (props) => <DrinkDetails { ...props } /> }
        />
        <Route path="/drinks" component={ Drinks } />
        <Route exact path="/explore" component={ Explore } />
        <Route
          path="/explore/meals/ingredientes"
          component={ MealIngredients }
        />
        <Route path="/explore/meals/area" component={ MealAreas } />
        <Route exact path="/explore/meals" component={ ExploreMeals } />
        <Route
          path="/explore/drinks/ingredientes"
          component={ DrinkIngredients }
        />
        <Route exact path="/explore/drinks" component={ ExploreDrinks } />
        <Route path="/perfil" component={ Profile } />
        <Route path="/finished-recipes" component={ FinishedRecipes } />
        <Route path="/favorite-recipes" component={ FavoriteRecipes } />
        <Route component={ NotFound } />
      </Switch>
    </div>
  );
}

export default App;
